<template>
  <div class="modal-overlay" v-if="isVisible">
    <div class="modal-container">
      <!-- Îã´Í∏∞ Î≤ÑÌäº -->
      <button class="close-button" @click="closeModal">√ó</button>

      <!-- ÎÇ†Ïßú Ï†úÎ™© -->
      <h2>{{ formattedDate }}</h2>

      <!-- Í∞êÏ†ï ÏÑ†ÌÉù -->
      <div class="emoji-section">
        <p>Ïò§ÎäòÏùò Í∞êÏ†ï</p>
        <div class="emoji-options">
          <span
            v-for="emoji in emojis"
            :key="emoji"
            class="emoji-option"
            :class="{ selected: selectedEmoji === emoji }"
            @click="selectEmoji(emoji)"
          >
            {{ emoji }}
          </span>
        </div>
        <button class="emoji-save-button" @click="saveEmoji">Ïù¥Î™®ÏßÄ Ï†ÄÏû•!</button>
      </div>

      <!-- Í∞ÑÎã®Ìïú ÏùºÍ∏∞ ÏûëÏÑ± -->
      <textarea
        class="diary-input"
        v-model="diaryText"
        placeholder="Í∞ÑÎã®Ìïú ÌïòÎ£® ÏùºÍ∏∞Î•º ÏûëÏÑ±Ìï¥Î≥¥ÏÑ∏Ïöî..."
      ></textarea>
      <button class="diary-save-button" @click="saveDiary">ÏùºÍ∏∞ Ï†ÄÏû•</button>

      <!-- Î£®Ìã¥ Î™©Î°ù -->
      <div class="routines" v-if="dayData?.routines?.length">
        <p>Ïò§ÎäòÏùò Î£®Ìã¥</p>
        <div
          v-for="(routine, index) in dayData.routines"
          :key="index"
          class="routine"
          :style="{ backgroundColor: routine.color }"
        >
          {{ routine.title }}
        </div>
      </div>

      <!-- Ìï† Ïùº Î™©Î°ù -->
      <div class="events" v-if="dayData?.todos?.length">
        <p>Ïò§ÎäòÏùò Ìï† Ïùº</p>
        <div
          v-for="(todo, index) in dayData.todos"
          :key="index"
          class="event"
          :class="{ done: todo.done }"
        >
          {{ todo.title }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {
    isVisible: {
      type: Boolean,
      required: true,
    },
    dayData: {
      type: Object,
      default: () => ({}),
    },
    formattedDate: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      emojis: ["üòÄ", "üò¢", "üò°", "üòÜ", "üòç"],
      selectedEmoji: null,
      diaryText: "",
    };
  },
  watch: {
    dayData: {
      immediate: true,
      handler(newData) {
        if (newData) {
          this.selectedEmoji = newData.emoji || null;
          this.diaryText = newData.diary || "";
        }
      },
    },
  },
  methods: {
    closeModal() {
      this.$emit("close", { emoji: this.selectedEmoji, diary: this.diaryText });
    },
    selectEmoji(emoji) {
      this.selectedEmoji = emoji;
    },
    async saveEmoji() {
  try {
    if (this.selectedEmoji) {
      await axios.post("/doitu/api/calender/emoji", {
        date: this.formattedDate,
        emoji: this.selectedEmoji,
      });
      alert("Ïù¥Î™®ÏßÄÍ∞Ä Ï†ÄÏû•ÎêòÏóàÏäµÎãàÎã§!");
      this.$emit("close", { emoji: this.selectedEmoji, diary: this.diaryText }); // Î∂ÄÎ™®Î°ú Îç∞Ïù¥ÌÑ∞ Ï†ÑÎã¨
    }
  } catch (error) {
    console.error("Ïù¥Î™®ÏßÄ Ï†ÄÏû• Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
    alert("Ïù¥Î™®ÏßÄ Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
  }
},
    async saveDiary() {
      try {
    if (this.diaryText.trim() !== "") {
      await axios.post("/doitu/api/calender/diary", {
        date: this.formattedDate,
        diary: this.diaryText,
      });
      alert("ÏùºÍ∏∞Í∞Ä Ï†ÄÏû•ÎêòÏóàÏäµÎãàÎã§!");
      this.$emit("close", { emoji: this.selectedEmoji, diary: this.diaryText }); // Î∂ÄÎ™®Î°ú Îç∞Ïù¥ÌÑ∞ Ï†ÑÎã¨
    }
  } catch (error) {
    console.error("ÏùºÍ∏∞ Ï†ÄÏû• Ï§ë Ïò§Î•ò Î∞úÏÉù:", error);
    alert("ÏùºÍ∏∞ Ï†ÄÏû•Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
  }
    },
  },
};
</script>

<style scoped>
/* Î™®Îã¨ Ïò§Î≤ÑÎ†àÏù¥ Ïä§ÌÉÄÏùº */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Î™®Îã¨ Ïª®ÌÖåÏù¥ÎÑà Ïä§ÌÉÄÏùº */
.modal-container {
  background: white;
  padding: 20px;
  border-radius: 10px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  position: relative;
}

/* Îã´Í∏∞ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.close-button {
  background: none;
  border: none;
  font-size: 1.5rem;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

/* Í∞êÏ†ï ÏÑ†ÌÉù ÏÑπÏÖò */
.emoji-section {
  margin: 20px 0;
}

.emoji-options {
  display: flex;
  gap: 10px;
}

.emoji-option {
  font-size: 1.5rem;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.emoji-option:hover {
  transform: scale(1.2);
}

.emoji-option.selected {
  border: 2px solid #1976d2;
  border-radius: 50%;
  padding: 5px;
}

/* ÏùºÍ∏∞ ÏûÖÎ†•ÎûÄ Ïä§ÌÉÄÏùº */
.diary-input {
  width: 90%;
  height: 100px;
  margin-top: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  resize: none;
}

/* Î£®Ìã¥ Î™©Î°ù */
.routines {
  margin-top: 20px;
}

.routine {
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 5px;
  color: white;
  text-align: center;
}

/* Ìï† Ïùº Î™©Î°ù */
.events {
  margin-top: 20px;
}

.event {
  padding: 10px;
  background: #e0f7fa;
  border-radius: 5px;
  margin-bottom: 5px;
}

.event.done {
  text-decoration: line-through;
  background: #c8e6c9;
}

/* Ï†ÄÏû• Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.emoji-save-button,
.diary-save-button {
  margin-top: 10px;
  background-color: #66bb6a;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  text-align: center;
  transition: background-color 0.3s ease;
}

.emoji-save-button:hover,
.diary-save-button:hover {
  background-color: #43a047;
}
</style>
