<template> 
  <div class="body">
    <div class="container">
      <Header
        :workingCount="workingCount"
        :doneCount="doneCount"
        @navigateToCalendar="goToCalendar"
        @filterStatus="filteredTasks"
      />
      <div class="content">
        <Sidebar :tagColors="tagColors" :days="days" @addTask="handleAddTask" />
        <div class="task-columns" >
          <TaskColumn
            :tasks="this.allTasks"
            :col="'left'"
            @markAsDone="markAsDone"
            @deleteTodo="deleteTodo"
            @deleteRoutine="deleteRoutine"
          />
        </div>
        <div class="task-columns" >
          <TaskColumn
            :tasks="this.allTasks"
            :col="'right'"
            @markAsDone="markAsDone"
            @deleteTodo="deleteTodo"
            @deleteRoutine="deleteRoutine"
          />
        </div>     
      </div>
    </div>
  </div>
</template>

<script>
import Header from "./Header.vue";
import Sidebar from "./TaskSideBar.vue";
import TaskColumn from "./TaskColumn.vue";
import axios from "axios";
axios.defaults.withCredentials = true; // Ïø†ÌÇ§Î•º Ï†ÑÏÜ°ÌïòÍ∏∞ ÏúÑÌï¥ ÏÑ§Ï†ï

export default {
  components: {
    Header,
    Sidebar,
    TaskColumn,
  },
  data() {
    return {
      allTasks: [],
      addTasks: [],
      allTaskCount: [], 
      col: {
      type: String,
      required: true,
      },
      tagColors: ["#80D8FF", "#FFD740", "#FFAB91", "#CE93D8", "#A5D6A7"],
      days: ["Ïùº", "Ïõî", "Ìôî", "Ïàò", "Î™©", "Í∏à", "ÌÜ†"],
      filterStatus: "all",
    };
  },
  computed: {
   workingCount() {
      return this.allTaskCount.routineDto.length + this.allTaskCount.todoDto.filter((task) => !task.done).length;
    },
    doneCount() {
      return this.allTaskCount.todoDto.filter((task) => task.done).length;
    },        
  },

  created() {// Ï≤òÏùå Ïã§Ìñâ Îê† Îïå Ïã§ÌñâÎêå 
    axios
      .get("/doitu/api/todoList/ALL")
      .then((response) => {
        if (response.data.statusCode === 200) {     
          this.allTasks = response.data || []; //Ìà¨ÎëêÎ¶¨Ïä§Ìä∏ Î£®Ìã¥ Î¶¨Ïä§Ìä∏ Í∞ÄÏ†∏Ïò¥ 
          this.allTaskCount = response.data || []; // working Í∞ØÏàò (ÏßÑÌñâÏ§ëÏù∏ Í≥ÑÌöç ) , done (ÎÅùÎÇú Í≥ÑÌöç Í∞ØÏàò ) 
          this.addIsRoutineToRoutineDto(); //ÏöîÏùº ÏÑ§Ï†ï ÎêòÏñ¥ ÏûàÏúºÎ©¥ Î£®Ìã¥ÏúºÎ°ú ÏÑ§Ï†ïÌïòÎäî Í≤É 
        } else {
          alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®");
        }
      })
      .catch((error) => {
        alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®: " + error.message);
      });
  },

  methods: {
    dataUP(){ // Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Îäî Î©îÏÑúÎìúÏù∏Îç∞ , ÏàòÏ†ïÎêú Îç∞Ïù¥ÌÑ∞Îì§ÏùÑ Î∞îÎ°ú Î∞òÏòÅÌïòÍ∏∞ ÏúÑÌïú Î©îÏÑúÎìú 
    let API;
      switch (this.filterStatus) { // working , done , all ÏÉÅÌÉúÏóê Îî∞Îùº Í∞ÄÏ†∏Ïò§Îäî Îã§Î•∏ APIÎ°ú ÏÑ§Ï†ï 
      case "done": 
      API = "/doitu/api/todoList/DONE";
      break;
  
      case "working":
      API = "/doitu/api/todoList/WORKING";
      break; 

      default:
      API = "/doitu/api/todoList/ALL";
      break;
      }

      axios
        .get(API)
        .then((response) => {
          if (response.data.statusCode === 200) {
            this.allTasks = response.data || []; 
            this.addIsRoutineToRoutineDto();
            this.TaskCount(); //Îç∞Ïù¥ÌÑ∞ ÏóÖÎ°úÎìú Ìï†Îïå ÎßàÎã§ working done Í∞ØÏàòÏÑ∏Í∏∞ ÏúÑÌï¥ Ï°¥Ïû¨ 
          } else {
            alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®");
          }
        })
        .catch((error) => {
          alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®: " + error.message);
        });  
      },


    async handleAddTask(newTask) { // sidebarÏóêÏÑú Í≥ÑÌöç Ï∂îÍ∞ÄÌïòÎäî Î°úÏßÅ 
      const routineData = {
        sun: newTask.routine.includes("Ïùº"),
        mon: newTask.routine.includes("Ïõî"),
        tue: newTask.routine.includes("Ìôî"),
        wed: newTask.routine.includes("Ïàò"),
        thr: newTask.routine.includes("Î™©"),
        fri: newTask.routine.includes("Í∏à"),
        sat: newTask.routine.includes("ÌÜ†"),
      };

      // ÏàúÏÑúÎ•º Î≥¥Ïû•Ìïú Í∞ùÏ≤¥ ÏÉùÏÑ±
      const payload = {
        title: newTask.title, // ÏàúÏÑú 1
        content: newTask.content, // ÏàúÏÑú 2
        sun: routineData.sun, // ÏàúÏÑú 3
        mon: routineData.mon, // ÏàúÏÑú 4
        tue: routineData.tue, // ÏàúÏÑú 5
        wed: routineData.wed, // ÏàúÏÑú 6
        thr: routineData.thr, // ÏàúÏÑú 7
        fri: routineData.fri, // ÏàúÏÑú 8
        sat: routineData.sat, // ÏàúÏÑú 9
        color: newTask.tag || "BLUE", // ÏàúÏÑú 10
      };

      console.log("Payload being sent:", payload);

      try {
        const response = await axios.post(
          "/doitu/api/todoList/create",
          payload
        );
        console.log("Task created:", response.data);
        this.addTasks.push({ ...response.data, done: false });
        this.dataUP();
      } catch (error) {
        console.error("Error creating task:", error.response || error.message);
        alert("Task ÏÉùÏÑ± Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. " + error.message);
      }
    },
     
    addIsRoutineToRoutineDto() { // ÏöîÏùº Ï°¥Ïû¨ÌïòÎ©¥ Î£®Ìã¥ÏúºÎ°ú ÏÑ§Ï†ï 
      if (this.allTasks.routineDto && Array.isArray(this.allTasks.routineDto)) {
        this.allTasks.routineDto = this.allTasks.routineDto.map((task) => {
          // ÏöîÏùºÏù¥ trueÏù∏ Í≤ΩÏö∞ Ìï¥Îãπ ÏöîÏùº Ïù¥Î¶ÑÏùÑ day Î∞∞Ïó¥Ïóê Ï∂îÍ∞Ä
          const day = [];
          if (task.sun) day.push("Ïùº");
          if (task.mon) day.push("Ïõî");
          if (task.tue) day.push("Ìôî");
          if (task.wed) day.push("Ïàò");
          if (task.thr) day.push("Î™©");
          if (task.fri) day.push("Í∏à");
          if (task.sat) day.push("ÌÜ†");

        // day Î∞∞Ïó¥Ïóê Í∞íÏù¥ ÏûàÏúºÎ©¥ isRoutine: true, ÏóÜÏúºÎ©¥ false
          return {
            ...task,
            day, // ÏöîÏùº Î∞∞Ïó¥ Ï∂îÍ∞Ä
            isRoutine: day.length > 0,
          };
        });

        console.log("Updated routineDto with day and isRoutine:", this.allTasks.routineDto);
      } else {
        console.error("routineDto is not an array or is undefined");
      }
    },

  // Todo ÏÇ≠Ï†ú Î©îÏÑúÎìú

    async deleteTodo(taskId) {
      try {
          const response = await axios.delete(`/doitu/api/todoList/todo/delete/${taskId}`);
          if (response.status === 200) {
              this.dataUP();
          }
      } catch (error) {
          console.error("ÏÇ≠Ï†ú ÏöîÏ≤≠ Ï§ë Ïò§Î•ò Î∞úÏÉù:", error.message);
      }
    },

  // Routine ÏÇ≠Ï†ú Î©îÏÑúÎìú
    async deleteRoutine(routineId) {
      try {
        const response = await axios.delete(`/doitu/api/todoList/routine/delete/${routineId}`);
        if (response.data.statusCode === 200) {
              this.dataUP();
              console.log("ÏÇ≠Ï†ú ÏÑ±Í≥µ");
        } else {
          console.error("Routine ÏÇ≠Ï†ú Ïã§Ìå®: ", response.data);
        }
      } catch (error) {
        console.error("ÏÇ≠Ï†ú ÏöîÏ≤≠ Ï§ë Ïò§Î•ò Î∞úÏÉù: ", error.message);
      }
    },

    async markAsDone(taskId) {
    // Î°úÏª¨ ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
      const taskIndex = this.allTasks.todoDto.findIndex(task => task.id === taskId);
      if (taskIndex !== -1) {
        this.allTasks.todoDto[taskIndex].done = !this.allTasks.todoDto[taskIndex].done;
      }
      try {
      const response = await axios.post(`/doitu/api/todoList/done/${taskId}`);
      if (response.data.statusCode === 200) {
        this.dataUP();
      } else {
        alert("ÏÉÅÌÉú Î≥ÄÍ≤Ω Ïã§Ìå®: " + response.data.msg);
      }
      } catch (error) {
        console.error("API Ìò∏Ï∂ú Ï§ë Ïò§Î•ò Î∞úÏÉù:", error.message);
      }
    },
  
    filteredTasks(filterInput) { //ÌòÑÏû¨ working done all Î≤ÑÌäº ÎàÑÎ•ºÎïå ÌòÑÏû¨ ÏÉÅÌÉú Î≥ÄÌï®
      switch (filterInput) {
        case "done":                //doneÏù¥Î©¥ ÎÅùÎÇú Í≥ÑÌöçÎßå Î≥¥Ïó¨Ï£ºÍ≤å Ìï® 
          this.filterStatus = "done";
          this.dataUP();
        break;
        case "working":             // ÏßÑÌñâ Ï§ëÏù∏ Í≥ÑÌöç Îßå Î≥¥Ïó¨Ï§å 
          this.filterStatus = "working";
          this.dataUP();
        break;
        case "all":                 // Í≥ÑÌöç Î™®Îëê Î≥¥Ïó¨Ï§å 
        default:
          this.filterStatus = "all";
          this.dataUP();
        break;
      }
    },

    TaskCount() {                       //working done Í≥ÑÌöç Í∞ÅÍ∞Å Î™áÍ∞úÏù∏ÏßÄ ÏÑ∏Îäî Î©îÏÑúÎìú 
      axios
        .get('/doitu/api/todoList/ALL')
        .then((response) => {
          if (response.data.statusCode === 200) {
            this.allTaskCount = response.data || [];    
          } else {
            alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®");
          }
        })
        .catch((error) => {
          alert("Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®: " + error.message);
        });  
      },
},
  goToCalendar() {
      this.$router.push("/calendar");
  },

  mounted() {
  },


};
</script>

<style scoped>
.body {
  width: 100vw; /*ÎÑìÏù¥ Î≥¥Îäî ÌôîÎ©¥Ïóê ÎßûÏ∂∞ÏÑú Ï°∞Ï†à*/
  height: 100vh;
  font-family: Arial, sans-serif;
  background-color: #2dc987; /* ÎÖπÏÉâ Î∞∞Í≤Ω */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /* ÏàòÌèâ Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
}
.container {
  flex-direction: column;
  font-family: Arial, sans-serif;
  background-color: #e9f7ef;
  width: 80%;
  padding: 20px;
  margin: 20px;
  border-radius: 25px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}
.content {
  display: flex;
  flex: 1;
  gap: 20px;
  height: 100%;
}
.task-columns {
  flex: 1;
  display: flex;
}
</style>