<template>
  <div class = "task-column">
      <TaskCard
        v-for="(task, taskIndex) in filteredRoutineTasks"
        :key="task.id"
        :task="task"
        :index="taskIndex"
        @markAsDone="markAsDone"
        @deleteTask="deleteRoutine"
      />
      <TaskCard
        v-for="(task, taskIndex) in filteredTodoTasks"
        :key= "task.id"
        :task="task"
        :index="taskIndex"
        @markAsDone="markAsDone"
        @deleteTask="deleteTodo"
      />   
  </div>
</template>

<script>
import TaskCard from './TaskCard.vue';

export default {
  components: {
    TaskCard,
  },
  props: {
    tasks: {
      type: Object,
    },
    col: {
      type: String, // "left" ÎòêÎäî "right" Í∞íÏùÑ Î∞õÏùå
      required: true,
    },
  },
  computed: {
    filteredRoutineTasks() {
      if (this.col === "left") {
        // colÏù¥ "left"Ïù∏ Í≤ΩÏö∞ ÌäπÏ†ï Ï°∞Í±¥Ïùò Îç∞Ïù¥ÌÑ∞Î•º Î∞òÌôò
        return this.tasks.routineDto.filter((task, index) => index % 2 === 0);
      } else {
        // colÏù¥ "right"Ïù∏ Í≤ΩÏö∞
        return this.tasks.routineDto.filter((task, index) => index % 2 !== 0);
      }
    },
    filteredTodoTasks() {
    const isTodoLengthOdd = this.tasks.routineDto.length % 2 !== 0;

    if (this.col === "left") {
      // todoDtoÏùò Í∏∏Ïù¥Í∞Ä ÌôÄÏàòÏùº Îïå Î∞òÏ†Ñ
      if (isTodoLengthOdd) {
        return this.tasks.todoDto.filter((task, index) => index % 2 !== 0); // ÏõêÎûò routineDtoÏùò Î°úÏßÅ
      } else {
        return this.tasks.todoDto.filter((task, index) => index % 2 === 0); // ÏõêÎûò todoDtoÏùò Î°úÏßÅ
      }
    } else {
      // this.col === "right"
      if (isTodoLengthOdd) {
        return this.tasks.todoDto.filter((task, index) => index % 2 === 0); // ÏõêÎûò routineDtoÏùò Î°úÏßÅ
      } else {
        return this.tasks.todoDto.filter((task, index) => index % 2 !== 0); // ÏõêÎûò todoDtoÏùò Î°úÏßÅ
      }
    }
  },
  },
  methods: {
    markAsDone(taskId) {
      this.$emit("markAsDone", taskId);
    },
    deleteTodo(taskId) {
      this.$emit("deleteTodo", taskId);
    },
    deleteRoutine(taskId) {
      this.$emit("deleteRoutine", taskId);
    },
  },
};

</script>

<style scoped>
.task-column {
  flex: 1;
  padding: 8px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 80%;
  overflow: hidden;
}
</style>
